*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
  Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  font-size: 0.875rem;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.25rem;
}

h4 {
  font-size: 0.875rem;
}

p {
  margin-bottom: 1.25rem;
  color: rgb(15, 15, 15);
}

@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }

  h2 {
    font-size: 2.5rem;
  }

  h3 {
    font-size: 1.75rem;
  }

  h4 {
    font-size: 1rem;
  }

  body {
    font-size: 1rem;
  }

  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}

/*  global classes */

/* section */
.section {
  width: 90vw;
  margin: 0 auto;
}

@media screen and (min-width: 992px) {
  .section {
    width: 95vw;
  }
}
/*
=============== 
Color Generator
===============
*/

.container {
  text-align: center;
  display: flex;
  align-items: center;
  height: 100px;
  padding-left: 2rem;
}

.container h3 {
  margin-bottom: 0;
  margin-right: 2rem;
}

input {
  border-color: transparent;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;

}

.btn {
  background: rgb(151, 214, 233) ;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
  border-color: transparent; 
  border-color: rgb(129, 201, 219);
  text-transform: capitalize;
  cursor: pointer;
  }
  
  @media screen and (max-width: 576px) {
    .container h3 {
      font-size: 1rem;
    }
  
    input,
    .btn {
      font-size: 0.85rem;
    }
  }
  
  input.error {
    border: 2px solid rgb(185, 62, 62);
  }
  
  .colors {
    min-height: calc(100vh - 100px);
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(223.33px, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(96px, 1fr));
  }
  
  .color {
    padding: 1rem 2rem;
    cursor: pointer;
    font-size: 1rem;
    text-transform: none;
    border-radius: 50%;
    text-align: center;
    box-shadow: 3px 3px 3px #8a8685;
  }

  .color:hover{
    box-shadow: 5px 5px 10px #3a3736;
  }
  
  .percent-value {
    margin-top: 30%;
    margin-bottom: 0%;
    
  }
  
  .color-value {
    margin-top: 0%;
      margin-bottom: 30%;
  }
  
  .color-light .color-value {
    color: white;
  }
  
  .color-light .percent-value {
    color: white;
  }
  
  .alert {
    text-transform: uppercase;
    font-size: 0.85rem;
    margin-top: 0.5rem;
  }